const express = require('express');
const app = express();

const cors = require('cors');

app.use(cors());

app.get('/', function (req, res) {
    res.json({
        data: 'duc-tran-farmi-gsop-api'
    })
});


const port = process.env.PORT || 3000;
app.listen(port, () => {
    console.log('duc-tran-farmi-api listens on port ', port)
});